
import { computed, defineComponent, onMounted, PropType, ref } from "vue";
import SandContent from "./SandContent.vue";
import Suspension from "./Suspension.vue";
import RateAndDirection from "./RateAndDirection.vue";
import Salinity from "./Salinity.vue";
import FlowSandZ from "./FlowSandZ.vue";
import WaterLevelChart from "./WaterLevelChart.vue";
import { WaterLevelChartType } from "@/type";
export default defineComponent({
  components: {
    SandContent,
    Suspension,
    RateAndDirection,
    Salinity,
    FlowSandZ,
    WaterLevelChart,
  },
  props: {
    sandContentList: {
      type: Array,
    },
    suspensionList: {
      type: Array,
    },
    rateDirectionList: {
      type: Array,
    },
    salinityList: {
      type: Array,
    },
    flowSandZList: {
      type: Array,
    },
    tableNameList: {
      type: Array,
    },
    waterLevelChartInfo: {
      type: Object as PropType<WaterLevelChartType & { flag: boolean }>,
    },
  },
  setup(props) {
    const sandContent = ref();
    const suspension = ref();
    const rateAndDirection = ref();
    const salinity = ref();
    const flowSandZ = ref();
    const waterLevelChart = ref();
    const centerIndex = ref(0);
    const leftIndex = computed(() => {
      return (centerIndex.value - 1 + (tableNameList.value as string[]).length) % (tableNameList.value as string[]).length;
    });
    const rightIndex = computed(() => {
      return (centerIndex.value + 1) % (tableNameList.value as string[]).length;
    });
    const tableNameList = computed(() => {
      return props.tableNameList;
    });
    const sandContentList = computed(() => {
      return props.sandContentList;
    });
    const suspensionList = computed(() => {
      return props.suspensionList;
    });
    const rateDirectionList = computed(() => {
      return props.rateDirectionList;
    });
    const salinityList = computed(() => {
      return props.salinityList;
    });
    const flowSandZList = computed(() => {
      return props.flowSandZList;
    });

    const waterLevelChartInfo = computed(() => {
      return props.waterLevelChartInfo;
    });

    const changeExcel = async (handle: string) => {
      if (handle === "left") {
        centerIndex.value = (centerIndex.value - 1 + (tableNameList.value as string[]).length) % (tableNameList.value as string[]).length;
      } else {
        centerIndex.value = (centerIndex.value + 1) % (tableNameList.value as string[]).length;
      }

      if (sandContentList.value?.length != 0) {
        await sandContent.value.refreshData((sandContentList.value as string[])[centerIndex.value]);
      } else if (suspensionList.value?.length != 0) {
        await suspension.value.refreshData((suspensionList.value as string[])[centerIndex.value]);
      } else if (rateDirectionList.value?.length != 0) {
        await rateAndDirection.value.refreshData((rateDirectionList.value as string[])[centerIndex.value]);
      } else if (salinityList.value?.length != 0) {
        await salinity.value.refreshData((salinityList.value as string[])[centerIndex.value]);
      } else if (flowSandZList.value?.length != 0) {
        await flowSandZ.value.refreshData((flowSandZList.value as string[])[centerIndex.value]);
      } else if (waterLevelChart) {
        waterLevelChart.value.refreshData();
      }
    };

    onMounted(() => {
      console.log(props.waterLevelChartInfo);
    });

    return {
      sandContent,
      suspension,
      rateAndDirection,
      salinity,
      flowSandZ,
      tableNameList,
      sandContentList,
      suspensionList,
      rateDirectionList,
      salinityList,
      flowSandZList,
      centerIndex,
      leftIndex,
      rightIndex,
      waterLevelChartInfo,
      waterLevelChart,
      changeExcel,
    };
  },
});
